function addToCart(productIdOrObj) {
    let cart = JSON.parse(localStorage.getItem('cart') || '[]');
    if (typeof productIdOrObj === 'object' && productIdOrObj) {
        const keyMatch = (a,b)=> a.id===b.id && a.color===b.color && a.size===b.size;
        const existing = cart.find(it=>keyMatch(it, productIdOrObj));
        if (existing) existing.qty += productIdOrObj.qty || 1; else cart.push({ ...productIdOrObj, qty: productIdOrObj.qty || 1 });
    } else if (HOME_PRODUCTS[String(productIdOrObj)]) {
        const base = HOME_PRODUCTS[String(productIdOrObj)];
        const quick = { id: base.id, name: base.name, price: base.price, img: base.img, color: 'Black', size: 'M', qty: 1 };
        const existing = cart.find(it=> it.id===quick.id && it.color===quick.color && it.size===quick.size);
        if (existing) existing.qty += 1; else cart.push(quick);
    }
    localStorage.setItem('cart', JSON.stringify(cart));
    updateCartCount();
    showNotification('Added to cart!');
}

// Show notification
function showNotification(message) {
    // Create notification element
    const notification = document.createElement('div');
    notification.className = 'notification';
    notification.textContent = message;
    notification.style.cssText = `
        position: fixed;
        top: 20px;
        right: 20px;
        background: #000;
        color: white;
        padding: 12px 24px;
        border-radius: 8px;
        z-index: 9999;
        animation: slideIn 0.3s ease;
    `;
    
    document.body.appendChild(notification);
    
    // Remove notification after 3 seconds
    setTimeout(() => {
        notification.style.animation = 'slideOut 0.3s ease';
        setTimeout(() => {
            document.body.removeChild(notification);
        }, 300);
    }, 3000);
}

// Smooth scroll for navigation links
function scrollToSection(sectionId) {
    console.log('scrollToSection called with:', sectionId);
    console.log('Current page:', window.location.pathname);
    
    // Check if we're on the homepage
    const isHomePage = window.location.pathname.endsWith('index.html') || 
                      window.location.pathname.endsWith('/') || 
                      window.location.pathname === '' ||
                      window.location.pathname.includes('index.html');
    
    console.log('Is homepage:', isHomePage);
    
    if (!isHomePage) {
        // If not on homepage, navigate to homepage first
        console.log('Not on homepage, redirecting to index.html#' + sectionId);
        window.location.href = `index.html#${sectionId}`;
        return;
    }
    
    const target = document.getElementById(sectionId);
    if (target) {
        console.log('Scrolling to section:', sectionId, target);
        
        // For dynamic sections, wait a bit longer for content to load
        const delay = (sectionId === 'new-arrivals' || sectionId === 'top-selling') ? 300 : 100;
        
        setTimeout(() => {
            const finalTarget = document.getElementById(sectionId);
            if (finalTarget) {
                // Use scrollIntoView with scroll-margin-top for proper positioning
                finalTarget.scrollIntoView({
                    behavior: 'smooth',
                    block: 'start',
                    inline: 'nearest'
                });
            } else {
                console.log('Section not found:', sectionId);
            }
        }, delay);
    } else {
        console.log('Target element not found:', sectionId);
    }
}

